@import url(https://fonts.googleapis.com/css?family=Karla|Roboto:300,500&display=swap);
body {
  font-family: 'Karla', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #333;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

a {
  text-decoration: none;
  color: #2962FF;
}

p {
  font-size: 24px;
  line-height: 1.6;
}
#root {
  display: flex;
  min-height: 100vh;
  flex-direction: column; }
  #root .content {
    flex: 1 1; }

.container {
  width: 100%;
  max-width: 1720px;
  margin: auto;
  padding-left: calc(100vw - 100%); }

header {
  display: flex;
  padding-top: 24px;
  -webkit-animation: fadeIn 2s;
          animation: fadeIn 2s;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
  -webkit-animation-timing-function: ease-out;
          animation-timing-function: ease-out; }
  header .site-logo {
    flex: none;
    height: 112px; }
  header nav {
    flex: 1 1 auto;
    font-family: 'Roboto', sans-serif;
    font-weight: 500;
    font-size: 18px;
    letter-spacing: 4px;
    text-align: right; }
    header nav ul {
      list-style: none; }
      header nav ul li {
        display: inline; }
        header nav ul li a {
          display: inline-block;
          color: #333;
          text-transform: uppercase;
          padding: 8px 16px;
          position: relative;
          outline: 0; }
          header nav ul li a::after {
            content: '';
            width: 70%;
            height: 3px;
            display: inline-block;
            opacity: 0;
            border-radius: 2px;
            position: absolute;
            left: calc(50% - 70%/2);
            bottom: 0px;
            -webkit-transition: opacity 150ms ease-out, -webkit-transform 150ms ease-out;
            transition: opacity 150ms ease-out, -webkit-transform 150ms ease-out;
            transition: opacity 150ms ease-out, transform 150ms ease-out;
            transition: opacity 150ms ease-out, transform 150ms ease-out, -webkit-transform 150ms ease-out;
            -webkit-transform: translateY(-4px);
                    transform: translateY(-4px); }
          header nav ul li a.active::after, header nav ul li a:hover::after, header nav ul li a:focus::after {
            opacity: 1;
            -webkit-transform: translateY(0);
                    transform: translateY(0); }
          header nav ul li a.home-link.active::after, header nav ul li a.home-link:hover::after, header nav ul li a.home-link:focus::after {
            background: #f9d29f; }
          header nav ul li a.work-link.active::after, header nav ul li a.work-link:hover::after, header nav ul li a.work-link:focus::after {
            background: #c6e4b8; }
          header nav ul li a.resume-link.active::after, header nav ul li a.resume-link:focus::after, header nav ul li a.resume-link:hover::after {
            background: #f7e594; }
          header nav ul li a.contact-link.active::after, header nav ul li a.contact-link:focus::after, header nav ul li a.contact-link:hover::after {
            background: #ecbdb4; }
        header nav ul li:not(:first-child) {
          margin-left: 16px; }

.hero {
  margin: auto;
  display: block;
  -webkit-animation: slideUpFadeIn 1s;
          animation: slideUpFadeIn 1s;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
  -webkit-animation-timing-function: ease-out;
          animation-timing-function: ease-out; }

section.row {
  display: flex;
  max-width: 90%;
  margin: auto; }
  section.row img {
    max-width: 100%; }
  section.row .flex-none {
    flex: none; }
  section.row .flex-grow {
    flex: 1 1; }
  section.row.hello-section {
    margin-top: 300px; }
    section.row.hello-section .flex-grow {
      margin-left: 80px; }

footer {
  font-family: 'Roboto', sans-serif;
  background: #F2D7D4;
  margin-top: 120px;
  padding: 64px 32px; }
  footer .container {
    display: flex;
    min-height: 320px;
    align-items: center; }
    footer .container nav {
      display: block;
      font-family: 'Roboto', sans-serif;
      font-weight: 500;
      font-size: 18px;
      letter-spacing: 4px;
      text-transform: uppercase; }
      footer .container nav ul {
        list-style: none;
        margin: 0;
        padding: 0; }
        footer .container nav ul li a {
          display: inline-block;
          color: #333;
          padding: 8px 16px;
          outline: 0; }
    footer .container nav,
    footer .container .right-col {
      flex: 1 1; }
    footer .container img {
      flex: 2 1; }
    footer .container nav,
    footer .container .right-col {
      padding: 0 64px; }
    footer .container .page-break {
      width: 540px;
      display: block;
      margin: auto;
      padding: 32px 64px;
      opacity: 0.5; }
    footer .container .social-links a {
      margin-right: 12px; }
      footer .container .social-links a .social-link {
        display: inline-block;
        width: 44px;
        height: 44px;
        opacity: 0.8; }

.work-links {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin-top: 64px; }
  .work-links .work-link {
    flex-grow: 0;
    flex-shrink: 1;
    flex-basis: calc(50% - 64px);
    margin-bottom: 64px;
    -webkit-transition: opacity 200ms ease-out;
    transition: opacity 200ms ease-out; }
    .work-links .work-link div .img-wrapper {
      position: relative; }
      .work-links .work-link div .img-wrapper .overlay {
        opacity: 0;
        position: absolute;
        width: 100%;
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        background: #f00a;
        -webkit-transition: opacity 200ms ease-out;
        transition: opacity 200ms ease-out; }
        .work-links .work-link div .img-wrapper .overlay.work-link-panasonic {
          background: #488cf1aa; }
        .work-links .work-link div .img-wrapper .overlay.work-link-mtn {
          background: #ad145788; }
        .work-links .work-link div .img-wrapper .overlay.work-link-fitted {
          background: #ff8f0088; }
        .work-links .work-link div .img-wrapper .overlay.work-link-artwork {
          background: #6c937788; }
        .work-links .work-link div .img-wrapper .overlay.work-link-calligraphy {
          background: #f2d67e88; }
    .work-links .work-link:hover div .img-wrapper .overlay {
      opacity: 1; }
    .work-links .work-link div {
      display: flex; }
      .work-links .work-link div img {
        align-self: center; }
        .work-links .work-link div img:first-child {
          border-radius: 4px;
          position: relative; }
        .work-links .work-link div img:not(:first-child) {
          margin-left: 32px; }

@-webkit-keyframes fadeIn {
  0% {
    opacity: 0; }
  25% {
    opacity: 0; }
  100% {
    opacity: 1; } }

@keyframes fadeIn {
  0% {
    opacity: 0; }
  25% {
    opacity: 0; }
  100% {
    opacity: 1; } }

@-webkit-keyframes slideUpFadeInFullHeight {
  0% {
    opacity: 0;
    -webkit-transform: translateY(-100%);
            transform: translateY(-100%); }
  25% {
    opacity: 0; }
  100% {
    opacity: 1; } }

@keyframes slideUpFadeInFullHeight {
  0% {
    opacity: 0;
    -webkit-transform: translateY(-100%);
            transform: translateY(-100%); }
  25% {
    opacity: 0; }
  100% {
    opacity: 1; } }

@-webkit-keyframes slideUpFadeIn {
  0% {
    opacity: 0;
    -webkit-transform: translateY(40px);
            transform: translateY(40px); }
  25% {
    opacity: 0;
    -webkit-transform: translateY(40px);
            transform: translateY(40px); }
  100% {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0); } }

@keyframes slideUpFadeIn {
  0% {
    opacity: 0;
    -webkit-transform: translateY(40px);
            transform: translateY(40px); }
  25% {
    opacity: 0;
    -webkit-transform: translateY(40px);
            transform: translateY(40px); }
  100% {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0); } }

